<template>
  <section>
    <div class="inner">
      <h1 class="tit">내 강의실</h1>
      <div class="board-box board-list tc"> 
        <form id="search" name="search" action="" method="">
          <div class="search-box flex__e__m">            
            <select name="" id="" v-model="selectedValue" class="m-non">
              <option value="title" selected>제목</option>
              <option value="date">등록일</option>
              <option value="school">학교</option>
            </select>
            <div :class="{'option-show':selectedValue === 'date'}" class="search-type inline mgl10 clear">              
              <div class="item-date fl">
                <vue-date-picker v-model="date" range locale="ko" format="yyyy-MM-dd" :enable-time-picker="false" cancelText="취소" selectText="확인" class="vue-datapicker" />
              </div>
              <div class="fl">
                <input type="text" placeholder="검색어를 입력해주세요." title="검색 내용 입력">                
              </div>
              <button class="mgl10 inline-t fr"><span class="material-symbols-outlined inline-m">search</span></button>
            </div>   
          </div>
        </form>
        <div class="search-txt tl mgt10">총 <strong class="cl1">3</strong>개의 게시글이 있습니다.</div>
        <table>             
          <colgroup v-if="!isMobile">
            <col style="width:8%">
            <col style="width:30%">
            <col style="width:20%">
            <col style="width:15%">
            <col style="width:15%">
            <col style="width:12%">
          </colgroup>
          <colgroup v-if="isMobile">
            <col style="width:28%">
            <col style="width:28%">
            <col style="width:28%">
            <col style="width:16%">
          </colgroup>
          <tr>
            <th class="m-non t-m-non">번호</th>
            <th>표준 과목명</th>
            <th>강의 기간</th>
            <th>총 수강 시간</th>            
            <th class="m-non t-m-non">교수명</th>      
            <th>수강 여부</th>
          </tr>
          <tr @click="goToPage('/lms/myclass/view')" class="link-box">
            <td class="num m-non t-m-non">1</td>
            <td>차세대정보통신 강의1</td>
            <td>2024.01.02 ~ 2024.01.16</td>
            <td>8시간 30분</td>
            <td class="m-non t-m-non">홍길동</td>
            <td @click.stop class="c-auto"><button @click="openPopup('popupCancel')" type="button" class="ty2">취소</button></td>
          </tr>
          <!--
          <tr v-if="!isMobile">
            <td colspan="8" class="empty">등록된 게시글이 없습니다.</td>                
          </tr>
          <tr v-if="isMobile">
            <td colspan="4" class="empty">등록된 게시글이 없습니다.</td>                
          </tr>
          -->
        </table>

        <div class="pagination">
          <ul class="flex__m__c">
            <li class="ar"><router-link to="" class="block"><span class="material-symbols-outlined">keyboard_double_arrow_left</span></router-link></li>
            <li class="ar"><router-link to="" class="block"><span class="material-symbols-outlined">chevron_left</span></router-link></li>
            <li><router-link to="" class="on">1</router-link></li>
            <li><router-link to="">2</router-link></li>
            <li><router-link to="">3</router-link></li>
            <li><router-link to="">4</router-link></li>
            <li><router-link to="">5</router-link></li>
            <li class="ar"><router-link to="" class="block"><span class="material-symbols-outlined">navigate_next</span></router-link></li>
            <li class="ar"><router-link to="" class="block"><span class="material-symbols-outlined">keyboard_double_arrow_right</span></router-link></li>
          </ul>
        </div>          
      </div>          
    </div>
  </section>
</template>

<script>
import {ref, onMounted, getCurrentInstance } from 'vue';
import VueDatePicker from '@vuepic/vue-datepicker';
import '@vuepic/vue-datepicker/dist/main.css';

export default {
  components: { VueDatePicker },
  setup() {
    const { useMobile } = getCurrentInstance().appContext.config.globalProperties.$common;
    const { isMobile } = useMobile();
    const date = ref([]);
    onMounted(() => {
      const startDate = new Date();
      const endDate = new Date(new Date().setDate(startDate.getDate() + 7));
      date.value = [startDate, endDate];
    });
    return {
      date,
      isMobile,
    };
  },
  data() {
    return {
      selectedValue: 'title',
    };
  },
  methods: {
    goToPage(path) {
      this.$router.push(path);
    },
  },
};
</script>

<style scoped lang="scss">
@media (max-width: #{$laptop - 1px}) {
  .board-box.board-list td button, .board-box.board-list td .btn {
    width:6rem;
    height:4rem;    
    padding:0;
    line-height:4rem;
    border-radius:1rem;
  }
}
@media (max-width: #{$tablet - 1px}) {
  tr th:nth-child(2)::before {display:none;}
}
</style>